import React from 'react';
import { Fade } from 'react-reveal';
import './ProjectsPage.css';

function ProjectsPage() {
    return (
        <div id="projects-wrapper">
            <div id="projects-title">
                <h1>Projects I've Made</h1>
            </div>
            <div id="projects-content-container">
                <Fade left>
                    <div className="projects-content">
                        <div className="projects-content-title">
                            <h1>Vision Group</h1>
                        </div>
                        <div className="projects-content-content">
                            <p>A simple website made to a company called Vision</p>
                            <p><a href="vision-sample-website.herokuapp.com" target="_blank" rel="noreferrer">Here</a> is the link</p>
                        </div>
                    </div>
                </Fade>
                <Fade right>
                    <div className="projects-content left">
                        <div className="projects-content-title">
                            <h1>Smart Car</h1>
                        </div>
                        <div className="projects-content-content">
                            <p>A smart car that moves and avoid obstacles automatically</p>
                            <p>Made with arduino</p>
                        </div>
                    </div>
                </Fade>
                <Fade left>
                    <div className="projects-content">
                        <div className="projects-content-title">
                            <h1>Watermelon</h1>
                        </div>
                        <div className="projects-content-content">
                        <p>This website was made for <code>Certain time video sharing</code> feature</p>
                        <p>Unfortunatly Youtube added the new <code>Copy video URL at current time</code> But the page is still Upon running</p>
                        <p><a href="https://omarhosam.github.io/watermelon/" target="_blank" rel="norefferer">Here</a> is the link</p>
                        </div>
                    </div>
                </Fade>
            </div>
        </div>
    );
}

export default ProjectsPage;