import React from 'react';
import { Fade } from 'react-reveal';
import './TechPage.css';

function TechPage() {
    return (
        <div id="tech-wrapper">
            <div id="tech-title">
                <h1>Technologies I Can Use</h1>
            </div>
            <div id="tech-content-container">
                <Fade left>
                    <div className="tech-content">
                        <div className="tech-content-title">
                            <h1>Front-End Web Dev</h1>
                        </div>
                        <div className="tech-content-content">
                            <ul>
                                <li>HTML5</li>
                                <li>CSS3</li>
                                <li>Javascript</li>
                                <li>ReactJS</li>
                            </ul>
                        </div>
                    </div>
                </Fade>
                <Fade right>
                    <div className="tech-content left">
                        <div className="tech-content-title">
                            <h1>Backend Web Dev</h1>
                        </div>
                        <div className="tech-content-content">
                            <ul>
                                <li>Express.js</li>
                                <li>Node.js</li>
                            </ul>
                        </div>
                    </div>
                </Fade>
                <Fade left>
                    <div className="tech-content">
                        <div className="tech-content-title">
                            <h1>Databases</h1>
                        </div>
                        <div className="tech-content-content">
                            <ul>
                                <li>MySQL</li>
                                <li>SQLite3</li>
                            </ul>
                        </div>
                    </div>
                </Fade>
                <Fade right>
                    <div className="tech-content left">
                        <div className="tech-content-title">
                            <h1>Desktop Dev</h1>
                        </div>
                        <div className="tech-content-content">
                            <ul>
                                <li>Python</li>
                                <li>C++</li>
                                <li>C#</li>
                            </ul>
                        </div>
                    </div>
                </Fade>
                <Fade left>
                    <div className="tech-content">
                        <div className="tech-content-title">
                            <h1>Databases</h1>
                        </div>
                        <div className="tech-content-content">
                            <ul>
                                <li>MySQL</li>
                                <li>SQLite</li>
                            </ul>
                        </div>
                    </div>
                </Fade>
                <Fade right>
                    <div className="tech-content left">
                        <div className="tech-content-title">
                            <h1>Hardware</h1>
                        </div>
                        <div className="tech-content-content">
                            <ul>
                                <li>Arduino</li>
                            </ul>
                        </div>
                    </div>
                </Fade>
                <Fade left>
                    <div className="tech-content"  style={{
                        marginBottom: '50px'
                    }}>
                        <div className="tech-content-title">
                            <h1>Miscellaneous</h1>
                        </div>
                        <div className="tech-content-content">
                            <ul>
                                <li>Git</li>
                                <li>Heroku</li>
                                <li>Adobe Illustrator</li>
                                <li>Adobe Premiere Pro</li>
                            </ul>
                        </div>
                    </div>
                </Fade>
            </div>
        </div>
    );
}

export default TechPage;