import React from 'react';
import './AboutPage.css'

function AboutPage() {
    return (
        <div id="about-container">
            <div id="about-wrapper">
                <div id="about-title">
                    <h1>Who Am I</h1>
                </div>
                <div id="about-content">
                    <p>
                        My name is Omar Hosam and I'm from Egypt, I'm {new Date().getFullYear() - 2008} Years Old Right Now
                    </p>
                    <p>
                        I'm a Full Stack Web Developer and I'm a Software Engineer
                    </p>
                    <p>
                        I started learning programming at the age of 9 and started Web Developing at the age of 13
                    </p>
                    <p>
                        I like making programs with Python, Javascript, HTML, CSS and Hacking games with C++ and Cheat Engine
                    </p>
                    <p>
                        Besides of programming I also like Video Editing, Digital Art with Adobe Illustrator and Gaming
                    </p>
                </div>
            </div>
        </div>
    );
}

export default AboutPage;