import React from 'react';
import Typewriter from 'typewriter-effect';
import './HeroPage.css';

function HeroPage() {
    return (
        <div id="hero-container">
            <div id="hero-wrapper">
                <div id="hero-image">
                    <img src={require("../Assets/logo.png").default} alt="LOGO" />
                </div>
                <h1>Omar Hosam</h1>
                <div id="hero-typer">
                    <p>I'm a:</p>
                    <div id="hero-typer-text">
                        <Typewriter
                            options={{
                                strings: ['Web Developer', 'Software Engineer', 'Web Designer', "Full Stack Web Developer"],
                                autoStart: true,
                                loop: true,
                                delay: 200,
                                deleteSpeed: 20,
                                typeSpeed: 50,
                                cursor: '_',
                            }}
                        />
                    </div>
                </div>
            </div>
        </div>
    );
}

export default HeroPage;